import { Injectable } from '@angular/core';
import { HttpClient, HttpHeaders } from '@angular/common/http';
const httpoptions= new HttpHeaders({
  'user-key':'4fca57731b1e13a5b03c96e7f8519b5f'
})
@Injectable({
  providedIn: 'root'
})
export class RestroService {

  constructor(private http:HttpClient) { }
  getRestaurants(lattitude,logitude){
    let url="https://developers.zomato.com/api/v2.1/geocode?"+lattitude+'&lon='+logitude;
    return this.http.get(url,{headers:httpoptions});
     // (err)=>{console.log(err);};
    //});
  }
  
  getCuisines(res_id){
    
    let url="https://developers.zomato.com/api/v2.1/restaurant?res_id="+res_id;
    return this.http.get(url,{headers:httpoptions});
    
    
  }
}
